import * as React from 'react'
import { Grid } from '@material-ui/core'
import { useApolloQuery } from '@src/queries/__generated__/GetEventLogs.hooks'

interface IEventLogProps {
  transactionId: number
}
export const EventLog = (props: IEventLogProps) => {
  const { data } = useApolloQuery({
    variables: {
      filters: {
        offset: 0,
        limit: 10,
        entityId: props.transactionId.toString(),
      },
    },
  })
  if (data) {
    return (
      <div>
        {data.eventLogs.map((log) => (
          <div key={log.id}>
            <Grid container spacing={10}>
              <Grid item xs={3} sm={3}>
                <div>{log.eventName}</div>
              </Grid>
              <Grid item xs={3} sm={3}>
                <div>{log.createdTs}</div>
              </Grid>
              <Grid item xs={6} sm={6}>
                <div>
                  {log.changes.map((change, index) => (
                    <div key={index}>{change}</div>
                  ))}
                </div>
              </Grid>
            </Grid>
          </div>
        ))}
      </div>
    )
  }
  return <div />
}
