import { LinearProgress, List } from '@material-ui/core'
import * as React from 'react'
import { EventRow } from './EventRow'
import { useApolloQuery } from '@src/queries/__generated__/GetEvents.hooks'

interface Prop {}
export const Timeline: React.FC<Prop> = () => {
  const { loading, error, data } = useApolloQuery()
  if (loading) return <LinearProgress />
  if (error) return <p>Error :(</p>
  if (!data) throw new Error('bad')
  const events = [
    // Virtual checkpoint
    {
      __typename: 'ICheckPoint' as 'ICheckPoint',
      id: 0,
      eventType: 'CHECKPOINT',
      createdTs: new Date(),
      signedTs: null,
      signedBalance: null,
    },
    ...data.checkpoints,
    ...data.batches,
  ]
  events.sort((a, b) => {
    // Latest first
    return Date.parse(b.createdTs) - Date.parse(a.createdTs)
  })
  return (
    <div>
      <List>
        {events.map((e, index) => {
          return e ? <EventRow event={e} key={index} /> : null
        })}
      </List>
    </div>
  )
}
