import { GetCategories_categories } from 'src/queries/__generated__/GetCategories.hooks'

export interface Node extends GetCategories_categories {
  children: Array<Node>
}

/*
 * Recursive: add a node to a tree. If node has no parent, add to root.
 * Otherwise, add to its parent
 */
const addNode = (node: Node, tree, visited: Array<Node>, remaining: Array<Node>) => {
  // Add to root
  visited.push(node)
  const p = visited.find((x) => x.id === node.parent_id)

  if (!node.parent_id) {
    tree[node.id] = node
    return
  } else if (p) {
    // Find parent who is in visited attach
    const parentNode = p
    if (!parentNode.children.find((x) => x.id === node.id)) {
      parentNode.children.push(node)
    }
    return
  } else {
    //parent is not visited either
    const parentNode = remaining.find((v) => v.id === node.parent_id)
    if (!parentNode) {
      throw new Error('no parent cat')
    }
    if (!parentNode.children.find((x) => x.id === node.id)) {
      parentNode.children.push(node)
    }
    // const parentNode = { ...parentCat, children: [node] }
    return addNode(parentNode, tree, visited, remaining)
  }
}

export const getCategoryTree = (categories: Array<GetCategories_categories>) => {
  const tree = {}
  const visited: Array<Node> = []
  const remaining = categories.map((c) => {
    return { ...c, children: [] }
  })

  while (remaining.length > 0) {
    // Take node out of remaining
    const node = remaining.pop()
    if (!node) throw new Error()

    addNode(node, tree, visited, remaining)
  }

  return tree
}
