import * as React from 'react'
import { TableBody, TableHead, TableRow, TableContainer, Table, TableCell } from '@material-ui/core'
import { useApolloQuery } from '@src/queries/__generated__/GetModelRows.hooks'

interface TableProps {
  fields: Array<{ name: string; valueType: string }>
  modelId: string
}

const Body = ({ modelId }: { modelId: string }) => {
  const { data } = useApolloQuery({
    variables: {
      modelId: modelId,
    },
  })
  if (data) {
    return (
      <>
        {data.modelRows.map((row, index) => (
          <TableRow key={index}>
            {row.attributes.map((attrib) => (
              <TableCell key={attrib.name}>{attrib.value}</TableCell>
            ))}
          </TableRow>
        ))}
      </>
    )
  }

  return null
}
export default ({ fields, modelId }: TableProps) => {
  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            {fields.map((f) => (
              <TableCell key={f.name}>{f.name}</TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          <Body modelId={modelId} />
        </TableBody>
      </Table>
    </TableContainer>
  )
}
