import { Paper, LinearProgress, Button, Grid } from '@material-ui/core'
import * as React from 'react'
import { ImportForm } from './ImportForm'

import { NewTransactionForm } from './NewTransactionForm'
import { TransactionListAppBar } from './TransactionListAppBar'
import { TransactionRows } from './TransactionRow'
import { UrlStateContext } from '@src/Root/contexts'
import { useApolloQuery } from '@src/queries/__generated__/GetTransactions.hooks'
import { Timeline } from '@src/Root/Timeline'

type FormType = 'transaction' | 'wave'

const limit = 40
const isWide = window.innerWidth > 600

export const TransactionList = (props: {}) => {
  console.log('rerender entire list page')
  const urlFilters = React.useContext(UrlStateContext)
  const [showTimeline, setShowTimeline] = React.useState(isWide)
  const [formType, setFormType] = React.useState<FormType | null>(null)

  const { loading: isTransactionLoading, error, data, fetchMore } = useApolloQuery({
    variables: {
      filters: {
        offset: 0,
        limit,
        ...urlFilters,
      },
    },
  })
  if (error) {
    return <div>{error.message}</div>
  }

  const transactions = data?.transactions || []
  const categories = data?.categories || []
  // TODO: implement selectable
  const selectableCategories = categories.filter((c) => c)

  const loadMoreTransactions = () => {
    fetchMore({
      variables: { filters: { offset: transactions.length || -1, limit, ...urlFilters } },
      updateQuery: (prev, { fetchMoreResult }) => {
        if (!fetchMoreResult) return prev
        return Object.assign({}, prev, {
          transactions: [...prev.transactions, ...fetchMoreResult.transactions],
        })
      },
    })
  }

  const showForm = (formType: FormType) => {
    setFormType(formType)
  }
  return (
    <Paper variant="outlined">
      <div style={{ maxWidth: '100%' }}>
        <TransactionListAppBar
          toggleTimeline={() => setShowTimeline(!showTimeline)}
          showForm={showForm}
          balance={data?.balance.amount || '?'}
          categoryChoices={selectableCategories.map((c) => {
            return { id: c.id, label: c.description }
          })}
        />
        <div
          style={{
            height: window.innerHeight - 70,
            overflow: 'scroll',
          }}
        >
          <Grid container>
            {showTimeline ? (
              <Grid item sm={3}>
                <Timeline />
              </Grid>
            ) : null}
            <Grid item sm={showTimeline ? 9 : 12}>
              <div style={{ marginBottom: '20px' }}>
                {formType === 'transaction' ? (
                  <NewTransactionForm
                    selectableCategories={selectableCategories || []}
                    onClose={() => setFormType(null)}
                  />
                ) : null}
                {formType === 'wave' ? <ImportForm onClose={() => setFormType(null)} /> : null}
              </div>
              {isTransactionLoading ? (
                <LinearProgress />
              ) : (
                <TransactionRows categories={categories || []} transactions={transactions || []} />
              )}
              <Button fullWidth size="large" color="secondary" onClick={loadMoreTransactions}>
                ({transactions.length}) Click to Load More ...
              </Button>
            </Grid>
          </Grid>
        </div>
      </div>
    </Paper>
  )
}
