import * as React from 'react'

import { ButtonGroup, Button, Typography } from '@material-ui/core'
import { useApolloQuery } from '@src/queries/__generated__/AcceptPrediction.hooks'
import { GetTransactions_categories as ICategory } from '@src/queries/__generated__/GetTransactions.hooks'

interface SmartCatProp {
  predicatedSplit: boolean | null
  predicatedCategory: number | null
  transactionId: number
  openCategoryDialog: () => void

  selectableCategories: Array<ICategory>
}
const isWide = window.innerWidth > 600
export const SmartCatField: React.FC<SmartCatProp> = (props) => {
  const {
    predicatedSplit,
    selectableCategories,
    predicatedCategory,
    transactionId,
    openCategoryDialog,
  } = props

  const category = selectableCategories.find((x) => x.id === predicatedCategory)
  if (!category) {
    throw new Error('cannot find predicated category')
  }

  const [acceptPrediction, { loading, error }] = useApolloQuery({
    variables: { id: transactionId },
  })
  const split = predicatedSplit ? 'Split ' : ''
  if (error) return <div>{error.message}</div>
  if (loading) return <div>saving...</div>
  return (
    <div>
      <Typography variant={isWide ? 'body1' : 'caption'}>{`${split}${category.name}?`}</Typography>
      <ButtonGroup aria-label="outlined primary button group">
        <Button size="small" onClick={(e) => acceptPrediction()} variant="contained">
          YES
        </Button>
        <Button
          size="small"
          onClick={(e) => openCategoryDialog()}
          variant="contained"
          color="secondary"
        >
          NO
        </Button>
      </ButtonGroup>
    </div>
  )
}
