import * as React from 'react'
import { Grid, Divider } from '@material-ui/core'
import { Link, useRouteMatch, Switch, Route } from 'react-router-dom'
import { useApolloQuery, GetModels_models } from '@src/queries/__generated__/GetModels.hooks'
import Table from './Table'

export const Admin = () => {
  return <AdminRoutes />
}

const AdminRoutes = () => {
  const match = useRouteMatch()
  const { loading, data } = useApolloQuery()

  if (loading) return <div>loading</div>
  if (data) {
    return (
      <div>
        <ul>
          {data.models.map((model) => (
            <li key={model.id}>
              <Link to={`${match.url}/${model.id}`}>{model.id}</Link>
            </li>
          ))}
        </ul>
        <Switch>
          {data.models.map((model) => (
            <Route key={model.id} exact path={`${match.path}/${model.id}`}>
              <ModelPage model={model} />
            </Route>
          ))}
        </Switch>
      </div>
    )
  }
  return <div />
}

interface ModelPageProp {
  model: GetModels_models
}

const ModelPage = ({ model }: ModelPageProp) => {
  return (
    <div>
      <Grid container>
        <Grid item xs={6}>
          {model.id}:
          <div>
            {model.fields.map((f) => (
              <div key={f.name}>
                {f.name}: {f.valueType}{' '}
              </div>
            ))}
          </div>
        </Grid>
        <Grid item xs={6}>
          Filter Args:
          <div>
            {model.filterArgs.map((f) => (
              <div key={f.name}>
                {f.name}: {f.valueType}
              </div>
            ))}
          </div>
        </Grid>
      </Grid>
      <Table fields={model.fields} modelId={model.id} />
    </div>
  )
}
