import { createBrowserHistory } from 'history'
import * as _ from 'lodash'
import { UrlPathEnum } from '@src/types'

const pathMap: { [v: string]: UrlPathEnum } = {
  '': UrlPathEnum.Root,
  LoginPage: UrlPathEnum.LoginPage,
  TablesPage: UrlPathEnum.TablesPage,
  TransactionList: UrlPathEnum.TransactionList,
  CategoryPage: UrlPathEnum.CategoryPage,
  PnlChart: UrlPathEnum.PnlChart,
}

export const history = createBrowserHistory()
export const pushHistory = (
  pathname: UrlPathEnum,
  subpath = '',
  urlState?: {} | null,
  state: {} | null = null,
) => {
  const searchString =
    urlState && urlState && Object.keys(urlState).length
      ? '?urlState=' + JSON.stringify(_.pickBy(urlState, _.identity))
      : ''

  const pathValue = Object.keys(pathMap).find((k) => pathMap[k] === pathname)
  if (pathValue === undefined) {
    throw new Error(`cannot find ${pathname}`)
  }

  let path: string
  if (pathValue) {
    path = ['/' + pathValue, subpath].join('/')
  } else {
    path = [pathValue, subpath].join('/')
  }
  console.log(`pathname: ${pathname} pathValue: ${pathValue} subpath: ${subpath} path: ${path}`)

  return history.push({
    pathname: path,
    search: searchString,
    state,
  })
}

export const getPathName = (): UrlPathEnum => {
  const splitPaths = history.location.pathname.split('/').filter((p) => !!p)

  const stripped = splitPaths

  if (stripped.length === 0) {
    return UrlPathEnum.Root
  }
  /*
  else if (stripped.length !== 1) {
    throw new Error(`${stripped}: Expected exactly 1 path`)
  }
  */

  if (!pathMap[stripped[0]]) {
    throw new Error(`cannot find ${stripped}`)
  }

  return pathMap[stripped[0]]
}

const isLocal = !!window.location.port

export const BASE_URL = `${window.location.protocol}//${window.location.hostname}:${
  isLocal ? '8888' : ''
}`
