import * as React from 'react'
import { hot } from 'react-hot-loader'
import { client } from '@src/gqlClient'
import { ApolloProvider } from '@apollo/react-hooks'

import { TransactionList } from '@src/TransactionList'
import jwtDecode from 'jwt-decode'
import { useHistory, useLocation } from 'react-router-dom'

import { CategoryPage } from '@src/CategoryPage'
import { LoginPage } from '@src/LoginPage'
import { PnlChart } from '@src/Report/PnlChart'
import { Admin } from '@src/App/Admin'
import { Link, BrowserRouter, Switch, Route } from 'react-router-dom'

const Nav = () => {
  const location = useLocation()
  if (location.pathname !== '/') {
    return null
  }

  return (
    <nav>
      <ul>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/TransactionList">Transaction List</Link>
        </li>
        <li>
          <Link to="/ProfitAndLoss">Profit and Loss Chart</Link>
        </li>
        <li>
          <Link to="/LoginPage">Login</Link>
        </li>
        <li>
          <Link to="/CategoryPage">CategoryPage</Link>
        </li>
        <li>
          <Link to="/Admin">Admin</Link>
        </li>
      </ul>
    </nav>
  )
}

const Home = () => {
  console.log('Home')
  const authToken = sessionStorage.getItem('authToken')

  if (!authToken) {
    const history = useHistory()
    history.push('/LoginPage')
    return null
  }
  const decoded: any = jwtDecode(authToken)
  if ((decoded.exp as number) * 1000 < new Date().getTime()) {
    console.log(decoded.exp)
    return (
      <div>
        Token expired! Click here:
        <Link to="/LoginPage">Login</Link>
      </div>
    )
  }
  return (
    <div>
      <Nav />
      <Switch>
        <Route exact path="/ProfitAndLoss" component={PnlChart}></Route>
        <Route exact path="/TransactionList" component={TransactionList}></Route>
        <Route exact path="/CategoryPage" component={CategoryPage}></Route>
        <Route path="/Admin" component={Admin}></Route>
      </Switch>
    </div>
  )
}

/*
 * /LoginPage: render nav + login form
 * Otherwise:
 *  render Home:
 *    if !authToken:
 *      redirect to login page
 *    else:
 *      render sub routes
 *
 *
 */
const Routes = () => {
  return (
    <BrowserRouter>
      <div>
        <Switch>
          <Route exact path="/LoginPage" component={LoginPage}></Route>
          <Route path="/" component={Home}></Route>
        </Switch>
      </div>
    </BrowserRouter>
  )
}
export const App = hot(module)(() => (
  <ApolloProvider client={client}>
    <Routes />
  </ApolloProvider>
))
