import { IBatch, ICheckPoint, IEvent } from './types'

export const getEventDescription = (e: IEvent) => {
  if (e.eventType === 'CHECKPOINT') {
    const checkpoint = e as ICheckPoint
    return checkpoint.signedTs
      ? `balance: ${checkpoint.signedBalance}  Signed at ${new Date(
          checkpoint.signedTs,
        ).toLocaleDateString()}`
      : 'Not Signed'
  }
  const batch: IBatch = e as IBatch
  // bank uploads
  if (batch.origin != 'wave') {
    return `${batch.origin}: ${batch.startDate} TO ${batch.endDate}`
  }
  // Imported from wave or plaid
  if (e.eventType === 'BATCH_IMPORT') {
    const batch_import = e as IBatch
    return `${batch_import.origin}: ${batch_import.startDate.slice(
      0,
      10,
    )} TO ${batch_import.endDate.slice(0, 10)}`
  }

  throw new Error('not possible')
}
export const getEventIcon = (event: IEvent) => {
  if (event.eventType === 'CHECKPOINT') {
    return 'toll'
  }
  const batch: IBatch = event as IBatch
  // bank uploads
  if (batch.origin != 'wave') {
    return 'cloud_upload'
  }
  if (batch.eventType === 'BATCH_IMPORT') {
    const batch_import = batch
    return { plaid: 'grid_on', wave: 'waves' }[batch_import.origin]
  }
  throw new Error('not possible')
}

export const getEventButtonIcon = (event: IEvent) => {
  if (event.eventType === 'CHECKPOINT') {
    return 'plus_one'
  }
  return null
}
